<template>
    <div class="sworks">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 管理员管理
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container" v-loading="loading"> 
            <div class="form">
                <div class="form-top">
                    <el-button class="fr" type="primary" @click="editVisible=true" size="small">创建管理员用户</el-button>
                </div>
                <div class="cont">
                    <el-table
                      :data="tableData"
                      style="width: 100%;margin-top:20px;">
                      <el-table-column
                        type="index"
                        width="100px"
                        label="序号">
                      </el-table-column>
                      <el-table-column
                        prop="number"
                        label="编号">
                      </el-table-column>
                      <el-table-column
                        prop="name"
                        label="姓名">
                      </el-table-column>
                      <el-table-column label="操作" width='250px'>
                          <template slot-scope="scope">
                            <el-button
                              size="mini"
                              @click="handleEdit(scope.row)">编辑</el-button>
                            <el-button
                              size="mini"
                              type="info"
                              @click="resetpassword(scope.row)">重置密码</el-button>
                            <el-button
                              size="mini"
                              type="danger"
                              @click="delworks(scope.row.id)">删除</el-button>
                          </template>
                        </el-table-column>
                    </el-table>

                    <div class="pagination">
                        <el-pagination
                          background
                          :page-size="pageSize"
                          @current-change="changePage"
                          layout="prev, pager, next"
                          :total="total">
                        </el-pagination>
                    </div>
                    
                </div>
            </div>
        </div>
        <!-- 新建弹出框 -->
        <el-dialog title="创建管理员" class="datas-bg" :visible.sync="editVisible" width="380px">
            <el-input class="margin10" v-model="number" size="small" placeholder="请输入管理员编号"></el-input>
            <el-input class="margin10" v-model="name" size="small" placeholder="请输入管理员姓名"></el-input>
            <el-input class="margin10" v-model="password" size="small" placeholder="请输入管理员密码"></el-input>
            <el-button type="primary" @click="Addadmin" style="display:block;margin:0 auto;" size="small">确 定</el-button>
        </el-dialog>
        <!-- 编辑弹出框 -->
        <el-dialog title="编辑管理员" class="datas-bg" :visible.sync="editVisible2" width="380px">
            <el-input class="margin10" v-model="editItems.number" size="small" placeholder="请输入管理员编号"></el-input>
            <el-input class="margin10" v-model="editItems.name" size="small" placeholder="请输入管理员姓名"></el-input>
            <el-button type="primary" @click="Updateadmin" style="display:block;margin:0 auto;" size="small">确 定</el-button>
        </el-dialog>
    </div>
</template>

<script>
import {fetchAddadmin, fetchGetadminlist, fetchUpdateadmin, fetchDeleteadmin, fetchResetadminpassword, fetchGetadminbyid} from "../../axios/requests";
export default {
    name: 'course',
    data() {
        return {
            pageNum:1,
            pageSize:10,
            total:0,

            editItems:'',

            loading:false,
            name:'',
            number:'',
            password:'',
            editVisible:false,
            editVisible2:false,
            tableData: [],
        }; 
    },
    mounted(){
        this.Getadminlist()
    },
    methods: {
        openLink(){
            this.$router.push({path:'newworks'})
        },
        handleEdit(e){
            this.editVisible2=true;
            this.Getadminbyid(e.id)
        },
        resetpassword(e){
          this.editItems = Object.assign({}, e);
          this.$confirm('确定要重置密码吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.Resetadminpassword(e.id)
            })
            .catch(() => {});
        },
        delworks(e){
            this.$confirm('确定要删除此教师吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.Deleteadmin(e)
            })
            .catch(() => {});
        },
        changePage(page){
          this.pageNum = page;
          this.Getadminlist();
        },
        async Addadmin() {//添加老师
          let {number, name, password} = this;
            if (!number) {this.$message.error('编号不能为空');return};
            if (!name) {this.$message.error('姓名不能为空');return};
            if (!password) {this.$message.error('密码不能为空');return};
             const loading = this.$loading({
              lock: true,
              text: '加载中...',
              spinner: 'el-icon-loading'
            });
            try {
              let response = await fetchAddadmin(number, name, password);
              if (response.status.code ==0) {
                this.$message({
                  message: '添加成功',
                  type: 'success'
                });
                this.editVisible = false;
                this.Getadminlist()
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              loading.close()
            }
        },
        async Getadminlist() {//得到老师列表
            this.loading = true;
            let {pageNum, pageSize} = this;
            try {
              let response = await fetchGetadminlist(pageNum, pageSize);
              if (response.status.code ==0) {
                this.total = response.result.total;
                this.tableData = response.result.list;
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              this.loading = false;
            }
        },
        async Updateadmin() {//修改老师信息
          let editItems = this.editItems;
            if (!editItems.number) {this.$message.error('编号不能为空');return};
            if (!editItems.name) {this.$message.error('姓名不能为空');return};
             const loading = this.$loading({
              lock: true,
              text: '加载中...',
              spinner: 'el-icon-loading'
            });
            try {
              let response = await fetchUpdateadmin(editItems.id, editItems.number, editItems.name);
              if (response.status.code == 0) {
                  this.$message({
                    message: '修改成功',
                    type: 'success'
                  });
                  this.editVisible2 = false;
                this.Getadminlist();
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              loading.close()
            }
        },
        async Resetadminpassword(id) {//重置管理员密码
            const loading = this.$loading({
              lock: true,
              text: '加载中...',
              spinner: 'el-icon-loading'
            });
            try {
              let response = await fetchResetadminpassword(id);
              if (response.status.code ==0) {
                this.$message({
                  message: '重置成功，密码 admin1234',
                  type: 'success'
                });
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              loading.close()
            }
        },
        async Deleteadmin(id) {//删除老师
            const loading = this.$loading({
              lock: true,
              text: '加载中...',
              spinner: 'el-icon-loading'
            });
            try {
              let response = await fetchDeleteadmin(id);
              if (response.status.code ==0) {
                this.$message({
                  message: '删除成功',
                  type: 'success'
                });
                this.Getadminlist();
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              loading.close()
            }
        },
        async Getadminbyid(id) {//得到单个详情
            const loading = this.$loading({
              lock: true,
              text: '加载中...',
              spinner: 'el-icon-loading'
            });
            try {
              let response = await fetchGetadminbyid(id);
              if (response.status.code ==0) {
                this.editItems = response.result;
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              loading.close()
            }
        },
    }
};
</script>
<style lang='scss'>
    .datas-bg{
         .el-dialog__body{
            padding:10px 20px 30px;
         }
    }
</style>
<style lang='scss' scoped>
    .sworks{
        .form-top{
            overflow:hidden;
        }
    }
    .pagination{
        margin-top:20px;
    }
    .downloadbutton{
      margin-left:10px;
    }
    .margin10{
      margin-bottom:10px;
      width:100%;
    }
</style>


